* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.wrapper {
  width: 320px;
  margin: auto;
}

.header {
  height: 230px;
  background-image: url("head-home-320.864bba51.png");
  background-size: cover;
  padding-top: 44px;
  padding-left: 12px;
  padding-right: 12px;
}

.header-librery {
  height: 230px;
  background-image: url("head-library-320.ed45b3f6.png");
  background-size: cover;
  flex-direction: column;
  padding-top: 44px;
  padding-left: 12px;
  padding-right: 12px;
  display: flex;
}

.container-nav {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.logo {
  align-items: center;
  gap: 16px;
  text-decoration: none;
  display: flex;
}

.logo-link {
  color: #fff;
  font-size: 12px;
}

.logo-text {
  color: #fff;
  font-size: 30px;
}

.nav {
  gap: 50px;
  display: flex;
}

.nav-link {
  color: #fff;
  text-decoration: none;
  position: relative;
}

.active-link:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: red;
  position: absolute;
  bottom: 0;
  left: 0;
}

.search {
  width: 394px;
  margin: 48px auto 0;
  position: relative;
}

.search-librery {
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding-bottom: 30px;
  display: flex;
}

.input-librery {
  clip-path: inset(50%);
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.label-librery {
  color: #fff;
  cursor: pointer;
  border: 2px solid #fff;
  border-radius: 5px;
  padding: 10px 20px;
  position: relative;
}

.input-librery:checked + label {
  background-color: #ff6b01;
}

.search-input {
  width: 100%;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  font-size: 24px;
}

.search-button {
  height: 100%;
  cursor: pointer;
  background: none;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
}

.main {
  margin-top: 60px;
  margin-bottom: 60px;
}

.cinema-list ul {
  grid-template-columns: 1fr;
  justify-content: center;
  gap: 30px;
  margin-bottom: 60px;
  display: grid;
}

.film {
  list-style: none;
}

.link-film {
  text-decoration: none;
}

.img-film {
  width: 100%;
  height: auto;
}

.tytle-film {
  color: #000;
  width: 100%;
  font-size: 24px;
}

.ganre-film {
  width: 100%;
  color: #ff6b08;
  font-size: 24px;
}

@media screen and (min-width: 768px) {
  .wrapper {
    width: 768px;
  }

  .pagination {
    width: 50px;
    height: 50px;
  }

  .active-link:before {
    bottom: 10px;
  }

  .header {
    height: 230px;
    background-image: url("head-home-768.1b160e05.png");
    padding-left: 24px;
    padding-right: 24px;
  }

  .header-librery {
    height: 230px;
    background-image: url("head-library-768.891bf7af.png");
    padding-left: 24px;
    padding-right: 24px;
  }

  .search {
    width: 200px;
  }

  .container-nav {
    flex-direction: row;
    justify-content: space-between;
  }

  .cinema-list ul {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (min-width: 1280px) {
  .wrapper {
    width: 1280px;
  }

  .cinema-list ul {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .header {
    height: 216px;
    background-image: url("head-home-1280.22810667.png");
    padding-left: 42px;
    padding-right: 42px;
  }

  .header-librery {
    height: 216px;
    background-image: url("head-library-1280.8a1b0dc2.png");
    padding-left: 42px;
    padding-right: 42px;
  }

  .search {
    width: 350px;
  }
}

.pagination-list {
  grid-template: 1px / repeat(5, 30px);
  justify-content: center;
  gap: 10px;
  display: grid;
}

.pagination-container {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.pagination {
  height: 30px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.page {
  border: 1px solid #ff6b08;
}

.prev, .next {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.prev:hover, .next:hover, .pagination.active, .page:hover {
  background-color: #ff6b08;
}

.pagination:active {
  transition: all .1s;
  transform: scale(.8);
}

.pagination button {
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

